export default {
	data() {
		return {
			curData: {}
		}
	},
	// 接收父组件传值
	props: {
		data: Object, // 数据类型
		index: Number // 数据下标
	},
	methods: {
		// 关注
		tapFocus () {
			this.curData = this.data
			this.curData.isFocus = true
			// 官网 API-->界面 --> 交互反馈, 弹出框
			uni.showToast({
			    title: '关注成功',
			});
			this.toParentData()
		},
		// 操作顶和踩
		handleDC (type) {
			this.curData = this.data
			switch (type){
				case 'ding':
					// 如果当前状态是顶的状态不处理
					if (this.curData.dcType == '1') {
						return;
					}
					// 如果当前状态是踩，踩的数字--
					if (this.curData.dcType == '2') {
						this.curData.dcInfo.cNum--;
					}
					// 点赞数字加一
					this.curData.dcInfo.dzNum++;
					// 将当前状态变为点赞
					this.curData.dcType = '1'
					this.toParentData()
					break;
				case 'cai':
					// 如果当前状态是踩的状态不处理
					if (this.curData.dcType == '2') {
						return;
					}
					// 如果当前状态是顶，顶的数字--
					if (this.curData.dcType == '1') {
						this.curData.dcInfo.dzNum--;
					}
					// 踩数字加一
					this.curData.dcInfo.cNum++;
					// 将当前状态变为点踩
					this.curData.dcType = '2'
					this.toParentData()
					break;
				default:
					break;
			}
		},
		toParentData () {
			this.$emit('mergeData', this.curData, this.index)
			this.curData = {}
		}
	}
}
